*,
*::after,
*::before {
  box-sizing: border-box;
}
.document {
  height: 100%;
  scroll-behavior: smooth;
  overflow-x: hidden;
  padding: 0;
  margin: 0;
}
.page {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  font-family: "Coolvetica", "Arial", sans-serif;
}
.page__background {
  position: fixed;
  inset: 0;
  background-image: url("../img/shape.svg");
  background-repeat: round;
  background-size: 60px 130px;
  background-position: center;
  z-index: -1;
}
@font-face {
  font-family: "Coolvetica";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Coolvetica.woff2") format("woff2"), url("../fonts/Coolvetica.woff") format("woff");
}
@keyframes letter {
  0% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(0.8, 1.2);
  }
  50% {
    transform: scale(1.3, 0.7);
  }
  75% {
    transform: scale(0.9, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes nav-link {
  0% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(0.8, 1.2);
  }
  50% {
    transform: scale(1.3, 0.7);
  }
  75% {
    transform: scale(0.9, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  min-height: 100vh;
  background-image: url("../img/header-blop.svg");
  background-size: 1000px 800px;
  background-repeat: no-repeat;
  background-position: top left;
}
@media (max-width: 767px) {
  .header {
    background-size: 840px 680px;
    background-position: -20px -20px;
  }
}
.header__logo {
  color: #000c24;
  transition: 0.3s;
}
@media (max-width: 1135px) {
  .header__logo {
    color: #ffffff;
  }
}
.header__logo:hover {
  animation-name: letter;
  animation-duration: 0.6s;
  animation-timing-function: cubic-bezier(0.47, 0.82, 0.17, 1);
}
.header__logo:active {
  opacity: 0.3;
}
.letter {
  display: inline-block;
  transition: 0.3s;
}
.letter:hover {
  color: #003fbd;
  transition: 0.1s;
}
.navigation {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 100px;
}
@media (max-width: 767px) {
  .navigation {
    padding: 20px 40px;
  }
}
.navigation__toggler {
  display: none;
  width: 40px;
  height: 36px;
  z-index: 2;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  .navigation__toggler {
    display: block;
  }
}
.navigation__toggler-line {
  position: relative;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 4px;
  transition: 0.3s;
}
.navigation__toggler-line::before {
  position: absolute;
  top: 8px;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 4px;
  content: "";
  transition: 0.3s;
}
.navigation__toggler-line::after {
  position: absolute;
  top: -8px;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 4px;
  content: "";
  transition: 0.3s;
}
.navigation__toggler--active .navigation__toggler-line {
  transform: translate3d(-100px, 0, 0);
}
.navigation__toggler--active .navigation__toggler-line::before {
  transform: translate3d(100px, -8px, 0) rotate(45deg);
}
.navigation__toggler--active .navigation__toggler-line::after {
  transform: translate3d(100px, 8px, 0) rotate(-45deg);
}
.navigation__list {
  display: flex;
  padding: 0;
  margin: 0;
  gap: 50px;
  list-style-type: none;
  transition: 0.4s;
}
@media (max-width: 767px) {
  .navigation__list {
    position: absolute;
    top: 0;
    left: -100vw;
    height: 100vh;
    width: 50vw;
    padding-top: 100px;
    padding-left: 60px;
    flex-direction: column;
    background-color: #000c24;
    z-index: 1;
  }
}
.navigation__list--active {
  top: 0;
  left: 0;
}
.navigation__link {
  color: #ffffff;
  text-decoration: none;
  opacity: 0.6;
  transition: 0.3s;
}
.navigation__link:hover {
  opacity: 1;
  text-shadow: 0 0 20px #ffffff;
}
.navigation__link--active {
  text-shadow: 0 0 10px #ffffff;
  z-index: 1;
  opacity: 1;
}
@media (max-width: 767px) {
  .navigation__link--active {
    color: #ffffff;
    text-shadow: 0 0 10px #ffffff;
  }
}
.navigation__toggler:hover .navigation__toggler-line {
  box-shadow: 0 0 10px #ffffff;
}
.navigation__toggler:hover .navigation__toggler-line::before {
  box-shadow: 0 0 10px #ffffff;
}
.navigation__toggler:hover .navigation__toggler-line::after {
  box-shadow: 0 0 10px #ffffff;
}
.hero {
  padding-left: 100px;
  padding-top: 40px;
}
@media (max-width: 767px) {
  .hero {
    padding-left: 40px;
    padding-top: 20px;
  }
}
.hero__title {
  margin: 0;
  color: #ffffff;
  font-size: 60px;
  letter-spacing: -5px;
  cursor: default;
}
@media (max-width: 767px) {
  .hero__title {
    font-size: 40px;
  }
}
.hero__letter {
  transform: scale(0);
  transition: 0.3s;
  cursor: default;
}
.hero__description {
  margin: 40px 0;
  color: #ffffff;
  font-size: 18px;
  line-height: 500px;
  transition: 0.6s;
  opacity: 0;
}
.hero__social-list {
  display: flex;
  padding: 0;
  gap: 30px;
  list-style-type: none;
}
.hero__social-button {
  color: #ffffff;
  transition: 0.3s;
}
.hero__social-button:hover {
  color: #003fbd;
}
.hero__social-button:active {
  opacity: 0.6;
}
.animated {
  animation-name: letter;
  animation-duration: 0.6s;
  animation-timing-function: cubic-bezier(0.47, 0.82, 0.17, 1);
  animation-fill-mode: forwards;
}
.main {
  position: relative;
  flex-grow: 1;
}
.main__up-btn {
  position: fixed;
  bottom: 40px;
  right: 40px;
  display: none;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  z-index: 2;
  cursor: pointer;
}
@media (max-width: 767px) {
  .main__up-btn {
    bottom: 20px;
    right: 20px;
  }
}
.section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  min-height: 100vh;
  padding: 300px;
  color: #000c24;
  gap: 40px;
}
@media (max-width: 767px) {
  .section {
    grid-template-columns: 1fr;
    padding: 100px 0;
  }
}
@media (min-width: 768px) and (max-width: 1100px) {
  .section {
    padding: 300px 60px;
  }
}
.section--odd {
  color: #ffffff;
  background-color: #000c24;
  box-shadow: 0 0 30px 10px #000c24;
}
.section__text-wrapper {
  display: flex;
  padding: 0 40px;
  flex-direction: column;
  grid-column: 1;
  align-items: flex-start;
  transform: translateX(-1000px);
  transition: 0.8s;
}
@media (min-width: 768px) and (max-width: 1100px) {
  .section__text-wrapper {
    padding-right: 0;
  }
}
.section__title {
  font-size: 50px;
  margin: 0;
  text-transform: capitalize;
  transform: skewX(45deg) translateX(-250px);
  transition: 0.8s;
}
.section__title::first-letter {
  color: #003fbd;
}
.section__description {
  font-size: 18px;
  letter-spacing: 0.1em;
  transform: skewX(-80deg) translateX(-450px);
  transition: 0.8s;
  line-height: 150px;
}
.section__buttons-list {
  display: flex;
  padding: 0;
  gap: 40px;
  list-style-type: none;
}
.section__button {
  display: block;
  color: inherit;
  transition: 0.3s;
}
.section__button:hover {
  color: #003fbd;
}
.section__button:active {
  opacity: 0.6;
}
.section__image-wrapper {
  display: grid;
  align-items: end;
  grid-template-columns: repeat(8, 55px);
  transform: translateX(1000px) skewX(-45deg);
  transition: 0.7s;
  grid-row: 1;
  grid-column: 2;
}
@media (max-width: 767px) {
  .section__image-wrapper {
    justify-self: center;
    grid-template-columns: repeat(12, 30px);
    grid-column: 1;
  }
}
.section__picture {
  align-self: flex-end;
}
.section__picture--mobile {
  width: max-content;
  grid-column-start: 1;
  grid-row-end: -1;
  z-index: 1;
  transition: 0.3s;
}
.section__picture--desktop {
  width: max-content;
  grid-column-start: 2;
  grid-row-end: -1;
  z-index: -1;
}
.section__picture--tablet {
  width: max-content;
  grid-column-start: 9;
  grid-row-end: -1;
  z-index: 1;
}
.section__image {
  display: block;
  transition: 0.3s;
}
@media (max-width: 767px) {
  .section__image--mobile {
    width: 60px;
    height: auto;
  }
}
.section__image--mobile:hover {
  transform: translate3d(-10px, -20px, 0) rotate(-10deg);
}
@media (max-width: 767px) {
  .section__image--mobile:hover {
    transform: none;
  }
}
@media (max-width: 767px) {
  .section__image--desktop {
    width: 300px;
    height: auto;
  }
}
.section__image--desktop:hover {
  transform: translate3d(0, -40px, 0);
}
@media (max-width: 767px) {
  .section__image--desktop:hover {
    transform: none;
  }
}
@media (max-width: 767px) {
  .section__image--tablet {
    width: 120px;
    height: auto;
  }
}
.section__image--tablet:hover {
  transform: translate3d(10px, -20px, 0) rotate(10deg);
}
@media (max-width: 767px) {
  .section__image--tablet:hover {
    transform: none;
  }
}
.section--odd .section__text-wrapper {
  grid-column: 2;
  transform: translateX(1000px);
}
@media (min-width: 768px) {
  .section--odd .section__text-wrapper {
    align-items: end;
  }
}
@media (max-width: 767px) {
  .section--odd .section__text-wrapper {
    grid-column: 1;
  }
}
.section--odd .section__image-wrapper {
  grid-column: 1;
  transform: translateX(-1000px) skewX(45deg);
}
.section--odd .section__title {
  transform: skewX(-45deg) translateX(250px);
  text-align: right;
}
@media (max-width: 767px) {
  .section--odd .section__title {
    text-align: left;
  }
}
.section--odd .section__description {
  text-align: right;
}
@media (max-width: 767px) {
  .section--odd .section__description {
    text-align: left;
  }
}
.footer {
  padding: 8px 40px;
  background-color: #ffffff;
  z-index: 1;
}
.footer__social {
  display: grid;
  padding: 0;
  margin: 0;
  grid-template-columns: repeat(4, 40px);
  list-style-type: none;
  justify-items: center;
}
.footer__social-button {
  display: flex;
  color: #000c24;
  transition: 0.3s;
}
.footer__social-button:hover {
  color: #003fbd;
}
